.account {
  padding: 100px 0 80px;

  @include vp--1239 {
    padding-top: 60px;
  }

  @include vp--768 {
    padding-bottom: 40px;
  }
}

.account__title {
  position: relative;

  margin: 0;
  padding-bottom: 21px;

  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.21;
  text-transform: uppercase;

  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    right: 0;

    width: 100%;
    height: 1px;

    background-image: $green-to-yellow-right;
  }
  @include vp--425 {
    font-size: 14px;
  }
}

.account__wrapper {
  display: grid;
  grid-template-columns: 360px 1fr;
  gap: 40px;

  @include vp--1023 {
    grid-template-columns: 295px 1fr;
  }

  @include vp--768 {
    display: block;
  }
}

.account__menu {
  position: relative;

  padding: 40px 20px;

  background-color: rgba($color-olive-drab, 0.05);

  @include vp--768 {
    padding: 0;
  }
}

.account__menu-btn {
  position: relative;
  z-index: 3;
  display: none;

  width: 100%;
  padding: 20px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.43;
  text-transform: none;

  color: $color-olive-drab;

  background-color: #f8f9f3;
  border: none;
  box-shadow: none;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:not(:disabled):hover,
    &:focus {
      color: $color-semi-sweet-chocolate;
      background-color: #f8f9f3;
    }
  }

  @include vp--768 {
    display: flex;
    justify-content: space-between;
    text-align: left;
  }
}

.account__menu-btn-icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-left: auto;

  background-color: $color-semi-sweet-chocolate;

  transition: transform $trans--600;

  clip-path: polygon(25% 40%, 75% 40%, 50% 65%);

  .account__menu.open & {
    transform: rotate(180deg);
  }
}

.account__menu-content {
  @include vp--768 {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;

    max-height: 60px;
    overflow: hidden;

    visibility: hidden;
    opacity: 0;

    box-shadow: none;

    transition: max-height $trans--600, box-shadow $trans--600, opacity $delay--600, visibility $delay--600;

    .account__menu.open & {
      visibility: visible;
      opacity: 1;

      box-shadow: $shaddow-list;

      transition: max-height $trans--600, box-shadow $trans--600, opacity 0s, visibility 0s;
    }
  }
}

.account__menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;

  background-color: $color-transparent;

  cursor: pointer;

  transition: background-color $trans--600;

  .account__menu.open & {
    background-color: rgba($color-semi-sweet-chocolate, 0.5);
  }
}

.account__menu-list {
  @include reset-list;

  &:not(:last-child) {
    margin-bottom: 40px;
    padding-bottom: 40px;

    border-bottom: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
  }
}

.account__menu-list-item {
  &:not(:last-child) {
    margin-bottom: 14px;
  }
  &.current > .account__menu-link {
    font-weight: bold;
  }

  @include vp--768 {
    &.current {
      margin-bottom: 0;
    }
  }
}

.account__menu-link {
  font-size: 14px;
  line-height: 1.43;

  color: $color-semi-sweet-chocolate;

  &:not([href]) {
    // font-weight: 600;

    color: $color-olive-drab;
  }

  @include vp--768 {
    &:not([href]) {
      display: none;
    }
  }
}

.account__instructions {
  &:not(:last-child) {
    margin-bottom: 40px;
    padding-bottom: 40px;

    border-bottom: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
  }

  p {
    margin: 0;

    font-style: italic;
    font-size: 12px;
    line-height: 1.5;

    color: rgba($color-semi-sweet-chocolate, 0.8);

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }
}

.account__content {
  padding-top: 40px;
}

.account__content-title-wrapper {
  display: flex;
  align-items: center;

  margin-bottom: 20px;

  @include vp--1023 {
    display: block;
  }
}

.account__content-title {
  margin: 0;

  font-weight: 600;
  font-size: 18px;
  line-height: 1.33;

  &--limit-width {
    width: 200px;
  }
  @include vp--768 {
    font-size: 14px;
  }
}

.account__notification {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.25;

  transition: max-height $trans--300;

  &--success {
    color: $color-fruit-salad;
  }

  &--error {
    color: $color-alizarin;
  }

  @include vp--1023 {
    overflow: hidden;
    max-height: 0;

    span {
      display: block;
      padding-top: 20px;
    }
  }
}

.account-form__input {
  width: 100%;
  padding: 0;

  &:not(:last-child) {
    margin-bottom: 10px;
  }
}

.account-form__select {
  width: 100%;
  padding-top: 0;
  padding-bottom: 0;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  .custom-select__list {
    width: calc(100% - 200px);
  }

  @include vp--1023 {
    .custom-select__list {
      width: 100%;
    }
  }
}

.account-form__file-upload {
  display: flex;
  align-items: center;
  margin: 20px 0 40px;

  @include vp--1023 {
    display: block;

    margin-top: 10px;
  }
}

.account-form__file-desc {
  flex: 0 0 180px;
  align-items: center;

  margin-right: 20px;

  @include vp--1023 {
    margin-right: 0;
    margin-bottom: 10px;
  }
}

.account-form__file-input {
  input {
    display: none;
  }
}

.account-form__file-label {
  display: flex;
  align-items: center;

  font-size: 14px;
  line-height: 1.43;
  text-decoration-line: underline;
  text-decoration-skip-ink: none;

  svg {
    width: 20px;
    height: 20px;
  }

  span:not(:first-child) {
    margin-left: 10px;
  }
}

.account-form__checkbox {
  width: 100%;
  padding: 0;

  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

.account {
  &__container {
    @include vp--1280 {
      padding: 0 30px;
    }
    @include vp--768 {
      padding: 0 70px;
    }
    @include vp--640 {
      padding: 0 35px;
    }

    @include vp--425 {
      padding: 0 20px;
    }
  }
}
._for-manufacturers {
  .account {
    &__title {
      &::before {
        background-image: $blue-to-light-blue-right;
      }
    }
    &__menu {
      background-color: rgba($color-frosty-sky, 0.05);
      &-inner {
        background-color: inherit;
        @include vp--768 {
          background-color: $color-white;
          padding: 70px 0 0 0;
        }
      }
      &-link {
        &:not([href]) {
          color: $color-blue-for-MF;
        }
        &:hover {
          color: $color-blue-for-MF !important;
        }
      }
      &-btn {
        color: $color-blue-for-MF;
        background-color: rgba($color-frosty-sky, 0.05);
      }
    }
    &-form {
      &__submit {
        &-btn {
          min-width: unset;
          border: 2px solid $color-light-azure-craiola;
          background-color: $color-light-azure-craiola;
          &:hover,
          &:focus {
            color: $color-light-azure-craiola !important;
          }
        }
      }
    }
    &__content {
      width: 100%;
      overflow: hidden;
      &-table {
        &-block {
          width: 100%;
          overflow-x: auto;
        }
        &-box {
          width: 780px;
        }
      }
      &-table {
        width: 780px;
        border: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
        border-collapse: collapse;
        border-spacing: 0;
        thead {
          width: 100%;
        }
        &-head {
          width: 100%;
          display: flex;
          th {
            flex: 0 0 100px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding: 10px;
            font-size: 12px;
            line-height: 16px;
            font-style: normal;
            font-weight: normal;
            color: $color-blue-for-MF;
            text-align: left;
            border: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
            &:first-of-type {
              flex: 0 0 189px;
            }
            &:last-of-type {
              flex: 0 0 90px;
              padding: 10px 0 10px 10px;
            }
          }
        }
        tbody {
          width: 100%;
          tr {
            width: 100%;
            display: flex;
            td {
              flex: 0 0 100px;
              display: flex;
              justify-content: flex-start;
              align-items: center;
              padding: 10px;
              font-size: 12px;
              line-height: 16px;
              font-style: normal;
              font-weight: normal;
              color: $color-semi-sweet-chocolate;
              text-align: left;
              border: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
              &:first-of-type {
                flex: 0 0 189px;
              }
              &:last-of-type {
                flex: 0 0 90px;
              }
            }
          }
        }
        &-title {
          width: inherit;
          font-style: normal;
          font-weight: normal;
          font-size: 16px;
          line-height: 20px;
          background-color: $color-blue-for-MF;
          color: $color-white;
          padding: 10px;
          height: 40px;
          span {
            position: absolute;
          }
        }
        &-title.table-finished-products {
          width: 805px;
        }
      }
      &-table.table-finished-products {
        width: 805px;
        th {
          &:nth-child(4) {
            flex: 0 0 125px;
          }
          &:last-of-type {
            flex: 0 0 90px;
          }
        }
        td {
          &:nth-child(4) {
            flex: 0 0 125px;
          }
          &:last-of-type {
            flex: 0 0 90px;
          }
        }
      }
      &-table.table-calculations {
        margin-top: 24px;
      }
      &-btn-block {
        width: 100%;
        margin-top: 20px;
        &-form {
          width: 100%;
          max-width: 580px;
          display: grid;
          grid-template-columns: 1fr 1fr;
          grid-template-rows: 1fr 1fr;
          grid-gap: 20px;
          .upload-container {
            grid-column-start: 1;
            grid-column-end: 3;
            width: 100%;
            height: 30px;
            // border: 1px solid rgba($color-semi-sweet-chocolate, 0.5);
            position: relative;
          }
          @include vp--1023 {
            grid-gap: 10px;
          }
          @include vp--425 {
            display: flex;
            flex-direction: column;
          }
        }
      }
      &-dwnld-btn {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: unset;
        max-width: 280px;
        width: 100%;
        border: 2px solid $color-light-azure-craiola;
        background-color: $color-light-azure-craiola;
        &:hover,
        &:focus {
          color: $color-light-azure-craiola !important;
        }
        @include vp--1023 {
          padding: 10px;
        }
        @include vp--425 {
          max-width: 100%;
        }
      }
      &-upload-btn-label {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: row-reverse;
        justify-content: flex-start;
        &:hover {
          cursor: pointer;
          text-decoration: underline;
        }
        svg {
          margin-right: 15px;
        }
      }
      &-upload-input {
        position: absolute;
        width: 0.1px;
        height: 0.1px;
        z-index: -10;
        opacity: 0;
        user-select: none;
      }
      &-sub-title {
        @include vp--425 {
          font-size: 12px;
        }
      }
    }
  }
}

._for-veterinarians {
  .account {
    &__menu {
      &-inner {
        background-color: inherit;
        @include vp--768 {
          background-color: $color-white;
          padding: 70px 0 0 0;
        }
      }
    }
  }
}

._for-pet-shops {
  .account {
    &__content {
      &-item {
        width: 100%;
        min-height: 80px;
        max-width: 780px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px;
        border: 1px solid #ede4de;
        margin-bottom: 10px;
        @include vp--640 {
          flex-direction: column;
          justify-content: flex-start;
          align-items: flex-start;
        }
        &-text {
          width: 100%;
          max-width: 480px;
          margin-right: 20px;
          @include vp--640 {
            margin-right: 0;
            margin-bottom: 20px;
          }
        }
        &-btn {
          max-width: 240px;
          width: 100%;
          @include vp--640 {
            max-width: 100%;
          }
        }
      }
      &-dwnld-btn {
        width: 100%;
      }
      &-loyalty {
        &-if-none {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
        }
        &-link {
          font-style: normal;
          font-weight: normal;
          font-size: 14px;
          line-height: 20px;
          text-decoration-line: underline;
          color: #017fd6;
          margin-top: 20px;
          &:hover {
            text-decoration: none;
          }
        }
        &-if-none._hidden {
          display: none;
        }
        &-if-exist {
          margin-top: 40px;
        }
        &-discount {
          &-title {
            font-weight: 600;
            font-size: 16px;
            line-height: 20px;
            color: $color-blue-for-MF;
            margin-bottom: 20px;
            @include vp--768 {
              font-size: 14px;
            }
          }
          &-text {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: $color-semi-sweet-chocolate;
          }
          &-table {
            margin-bottom: 40px;
            overflow: auto;
            table {
              width: 780px;
              border: 1px solid rgba($color-semi-sweet-chocolate, 0.6);
              border-collapse: collapse;
              border-spacing: 0;
              @include vp--768 {
                width: 628px;
              }
            }
            tr {
              &:last-of-type td {
                font-size: 14px;
                line-height: 20px;
                font-weight: bold;
              }
            }
            td {
              border: 1px solid rgba($color-semi-sweet-chocolate, 0.6);
              padding: 10px 20px;
              text-align: left;
              vertical-align: top;
              min-width: 100px;
              width: 100%;
            }
          }
        }
      }
      &-tabs {
        &-navbar {
          width: 100%;
          min-height: 40px;
          margin-bottom: 20px;
          display: flex;
          @include vp--640 {
            flex-direction: column;
          }
        }
        &-switch {
          flex: 1 1 auto;
          min-height: inherit;
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 16px;
          line-height: 20px;
          color: $color-semi-sweet-chocolate;
          background-color: #f5f1ee;
          border: 1px solid #ede4de;
          &_trainings {
            flex: auto;
            width: 50%;
          }
          @include vp--640 {
            width: 100%;
          }
        }
        &-switch.tabs__link._active {
          color: $color-white;
          background: #1cc4e3;
          border: 1px solid #1cc4e3;
        }
        &-block {
          display: none;
          &-items {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
            grid-gap: 20px;
            @include vp--1023 {
              grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
            }
            @include vp--425 {
              grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
            }
            &_trainings {
              grid-template-columns: 1fr;
              @include vp--1023 {
                grid-template-columns: 1fr;
              }
              @include vp--425 {
                grid-template-columns: 1fr;
              }
            }
          }
        }
        &-item {
          &-title {
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            color: $color-semi-sweet-chocolate;
            margin: 20px 0 5px 0;
            @include vp--425 {
              font-size: 12px;
            }
            &_article-title {
              font-weight: 600;
              font-size: 16px;
              line-height: 20px;
              color: $color-blue-for-MF;
              margin: 0 0 20px 0;
            }
          }
          &-link {
            display: block;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            text-decoration-line: underline;
            color: $color-blue-for-MF;
            @include vp--425 {
              font-size: 12px;
            }
            &-archive {
              display: block;
              font-weight: 300;
              font-size: 12px;
              line-height: 20px;
              text-decoration-line: underline;
              text-transform: uppercase;
              color: $color-semi-sweet-chocolate;
              margin-bottom: 5px;
              @include vp--425 {
                font-size: 10px;
              }
            }
          }
          &_trainings {
            border-bottom: 1px solid rgba($color-semi-sweet-chocolate, 0.3);
            &-box {
              display: flex;
              justify-content: flex-start;
              @include vp--1239 {
                flex-wrap: wrap;
              }
            }
            &-btn {
              margin: 0 0 20px auto;
              @include vp--1239 {
                margin: 20px auto 20px 0;
              }
            }
          }
          &-article {
            max-width: 420px;
            width: 100%;
            &-text {
              font-style: normal;
              font-weight: normal;
              font-size: 14px;
              line-height: 20px;
              color: $color-semi-sweet-chocolate;
              margin: 0;
            }
          }
          &-date-and-time {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            text-align: right;
            color: $color-semi-sweet-chocolate;
            margin-bottom: 20px;
            @include vp--1239 {
              margin-top: 20px;
              text-align: left;
            }
          }
          &-place,
          &-address {
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 20px;
            text-align: right;
            color: $color-blue-for-MF;
            @include vp--1239 {
              text-align: left;
            }
          }
          &-place {
            margin-bottom: 5px;
          }
        }
        &-block.tabs__item._active {
          display: block;
        }
        &-img-box {
          &_trainings {
            max-width: 120px;
            min-width: 120px;
            margin: 0 20px 20px 0;
            @include vp--425 {
              max-width: 100%;
              margin: 0 0 20px 0;
            }
          }
        }
      }
      &_distributors {
        margin-top: 40px;
        &-item {
          display: grid;
          grid-template-columns: 180px 1fr;
          grid-gap: 20px;
          margin-bottom: 20px;
          @include vp--425 {
            grid-template-columns: 1fr;
          }
        }
        &-img-box {
          min-width: 180px;
          max-width: 180px;
          @include vp--425 {
            max-width: 100%;
            height: 180px;
          }
        }
        &-img {
          width: 100%;
          height: inherit;
          object-fit: cover;
        }
        &-desc {
          display: flex;
          flex-direction: column;
          &-title {
            margin-bottom: 20px;
            h3 {
              margin: 0;
              font-weight: 500;
              font-size: 14px;
              line-height: 20px;
            }
          }
          &-address {
            font-weight: normal;
            font-size: 12px;
            line-height: 20px;
            margin-bottom: 10px;
          }
          &-phone,
          &-email {
            display: block;
            font-weight: 500;
            font-size: 12px;
            line-height: 20px;
            color: $color-semi-sweet-chocolate;
          }
          &-phone {
            margin-bottom: 5px;
          }
          &-btn {
            margin-top: auto;
            max-width: 280px;
            display: flex;
            justify-content: center;
            align-items: center;
            @include vp--425 {
              margin-top: 20px;
            }
          }
        }
      }
    }
  }
}
